</svg>`
                    },
                ],

                dragging: false,
                draggingIndex: 0,
                draggingObj: null,
                startEvent: null,
                startState: null
            }
        },

        methods: {
            skillCardOnMouseDown(e) {
                this.draggingIndex = parseInt(e.target.attributes['index'].value)
                this.dragging = true
                this.startEv = e

                this.draggingObj = copy(this.skills.splice(parseInt(e.target.attributes['index'].value), 1)[0])
                this.startState = copy(this.skills)

                this.skills.splice(parseInt(e.target.attributes['index'].value), 0, this.draggingObj)
            },
            skillCardOnMouseMove(e) {
                if (this.dragging) {
                    this.skills = copy(this.startState)
                    let i = parseInt(e.target.attributes['index'].value)
                    if (i > this.draggingIndex) {
                        this.skills.splice(i - 1, 0, this.draggingObj)
                        let to = this.skills.splice(i, 1)[0]
                        this.skills.splice(this.draggingIndex, 0, to)
                    } else if (i < this.draggingIndex) {
                        this.skills.splice(i, 0, this.draggingObj)
                        let to = this.skills.splice(i+1, 1)[0]
                        this.skills.splice(this.draggingIndex, 0, to)
                    } else {
                        this.skills.splice(i, 0, this.draggingObj)
                    }
                }
            },
            skillCardOnMouseUp() {
                if (this.dragging) {
                    this.dragging = false
                }
            },
        },

        components: {
            skill_card
        }
    }
</script>

